import React, { useState, useRef, useEffect } from 'react';
import ReactMarkdown from 'react-markdown';
import './App.css';

function App() {
  const [input, setInput] = useState('');
  const [messages, setMessages] = useState([]);
  const [isLoading, setIsLoading] = useState(false);
  const eventSourceRef = useRef(null);
  const messagesEndRef = useRef(null);
  
  // 
  const fullResponseRef = useRef('');
  const typingIntervalRef = useRef(null);
  const currentMessageIdRef = useRef(null);

  // 
  const scrollToBottom = () => {
    messagesEndRef.current?.scrollIntoView({ behavior: 'smooth' });
  };

  // 
  useEffect(() => {
    scrollToBottom();
  }, [messages]);

  //  EventSource 
  useEffect(() => {
    return () => {
      if (eventSourceRef.current) {
        eventSourceRef.current.close();
      }
      if (typingIntervalRef.current) {
        clearInterval(typingIntervalRef.current);
      }
    };
  }, []);

  // 
  const startTypingEffect = (messageId) => {
    // 
    if (typingIntervalRef.current) {
      clearInterval(typingIntervalRef.current);
    }

    // 
    let displayedLength = 0;
    
    // 
    typingIntervalRef.current = setInterval(() => {
      // 
      if (displayedLength < fullResponseRef.current.length) {
        displayedLength += 1;
        
        // 
        setMessages(prevMessages => {
          return prevMessages.map(msg => {
            if (msg.id === messageId) {
              return {
                ...msg,
                text: fullResponseRef.current.slice(0, displayedLength),
                streaming: true
              };
            }
            return msg;
          });
        });
      } else {
        // 
        clearInterval(typingIntervalRef.current);
        
        // 
        setMessages(prevMessages => {
          return prevMessages.map(msg => {
            if (msg.id === messageId) {
              return {
                ...msg,
                streaming: false
              };
            }
            return msg;
          });
        });
        
        // 
        fullResponseRef.current = '';
        currentMessageIdRef.current = null;
      }
    }, 20);
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    if (!input.trim()) return;

    // 
    if (eventSourceRef.current) {
      eventSourceRef.current.close();
    }
    if (typingIntervalRef.current) {
      clearInterval(typingIntervalRef.current);
    }

    // 
    const userMessage = { text: input, sender: 'user' };
    setMessages(prev => [...prev, userMessage]);
    
    //  AI 
    const aiMessageId = Date.now().toString();
    currentMessageIdRef.current = aiMessageId;
    
    // 
    fullResponseRef.current = '';
    
    //  AI 
    setMessages(prev => [...prev, { 
      id: aiMessageId,
      text: '',  // 
      sender: 'ai',
      streaming: true
    }]);
    
    setInput('');
    setIsLoading(true);

    try {
      //  SSE 
      const encodedMessage = encodeURIComponent(input);
      const apiUrl = `/api/chat-stream?message=${encodedMessage}`;
      
      eventSourceRef.current = new EventSource(apiUrl);
      
      // 
      eventSourceRef.current.onmessage = (event) => {
        try {
          const data = JSON.parse(event.data);
          if (data.token) {
            //  token 
            fullResponseRef.current += data.token;
            // 
      startTypingEffect(aiMessageId);
      
          }
          
          if (data.done) {
            //  SSE 
            if (eventSourceRef.current) {
              eventSourceRef.current.close();
              eventSourceRef.current = null;
            }
            setIsLoading(false);
          }
          
          if (data.error) {
            console.error('Stream error:', data.error);
            
            // 
            setMessages(prevMessages => {
              return prevMessages.map(msg => {
                if (msg.id === aiMessageId) {
                  return { 
                    ...msg,
                    text: 'Sorry, there was an error: ' + data.error,
                    streaming: false,
                    error: true
                  };
                }
                return msg;
              });
            });
            
            // 
            fullResponseRef.current = '';
            if (eventSourceRef.current) {
              eventSourceRef.current.close();
              eventSourceRef.current = null;
            }
            if (typingIntervalRef.current) {
              clearInterval(typingIntervalRef.current);
              typingIntervalRef.current = null;
            }
            setIsLoading(false);
          }
        } catch (err) {
          console.error('Error parsing SSE data:', err, event.data);
        }
      };
      
      
      // 
      eventSourceRef.current.onerror = (error) => {
        console.error('EventSource error:', error);
        
        // 
        setMessages(prevMessages => {
          return prevMessages.map(msg => {
            if (msg.id === aiMessageId) {
              return { 
                ...msg,
                text: 'Connection error. Please try again.',
                streaming: false,
                error: true
              };
            }
            return msg;
          });
        });
        
        // 
        fullResponseRef.current = '';
        if (eventSourceRef.current) {
          eventSourceRef.current.close();
          eventSourceRef.current = null;
        }
        if (typingIntervalRef.current) {
          clearInterval(typingIntervalRef.current);
          typingIntervalRef.current = null;
        }
        setIsLoading(false);
      };
      
    } catch (error) {
      console.error('Error creating EventSource:', error);
      
      // 
      setMessages(prevMessages => {
        return prevMessages.map(msg => {
          if (msg.id === aiMessageId) {
            return { 
              ...msg,
              text: 'Sorry, there was an error creating the connection.',
              streaming: false,
              error: true
            };
          }
          return msg;
        });
      });
      
      // 
      fullResponseRef.current = '';
      if (typingIntervalRef.current) {
        clearInterval(typingIntervalRef.current);
        typingIntervalRef.current = null;
      }
      setIsLoading(false);
    }
  };

  return (
    <div className="app">
      <header className="app-header">
        <h1 data-text=""></h1>
      </header>
      <div className="chat-container">
        <div className="decoration-element"></div>
        <div className="messages">
          {messages.length === 0 && (
            <div className="welcome-message">
              <h2></h2>
              <p>...</p>
            </div>
          )}
          {messages.map((message, index) => (
            <div 
              key={message.id || index} 
              className={`message ${message.sender} ${message.streaming ? 'streaming' : ''} ${message.error ? 'error' : ''}`}
            >
              {message.sender === 'user' ? (
                <div className="message-content">{message.text}</div>
              ) : (
                <div className="message-content">
                  <ReactMarkdown>{message.text}</ReactMarkdown>
                  {message.streaming && (
                    <span className="cursor"></span>
                  )}
                </div>
              )}
            </div>
          ))}
          <div ref={messagesEndRef} />
        </div>
      </div>
      
      {/*  */}
      <form className="input-form" onSubmit={handleSubmit}>
        <input
          type="text"
          value={input}
          onChange={(e) => setInput(e.target.value)}
          placeholder="..."
          disabled={isLoading}
        />
        <button type="submit" disabled={isLoading || !input.trim()}>
          
        </button>
      </form>
    </div>
  );
}

export default App; 